import React, {useState, useRef} from 'react';
import cookie from 'react-cookies'

const axios = require('axios');

console.log(cookie.load('bets'));

const bets = cookie.load('bets');


function Weeks(props){
    
    const weeks = props.weeks.map(week => {
        return  <Week week={week} key={week.id} />
    })
    return(<div>
        <h2>Matches</h2>
        <div className="Weeks">
        {weeks}
        </div>
        </div>
        
        
        );
        
    }
    function Week({week}){
        
        function timeConverter(UNIX_timestamp){
            var a = new Date(UNIX_timestamp);
            var months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
            var year = a.getFullYear();
            var month = months[a.getMonth()];
            var date = a.getDate();
            var time = date + ' ' + month + ' ' + year ;
            return time;
        }
        const Matchs = week.matches.map(match => {
            return  <Match match={match} key={match.id} />
        })
        return(
            <div className="Week">
            <div className="info flex">
            <div className="name">
            {week.name}
            </div>
            
            <div className="date start">
            {timeConverter(week.startDate)}
            </div>
            <div className="date separator">-</div>
            <div className="date end">
            {timeConverter(week.endDate)}
            </div>
            
            </div>
            
            <div className="Matchs flex center">
            {Matchs}
            </div>
            
            </div>
            )
        }
        function Match({match}){

        
            const TeamA = match.competitors[0];
            TeamA.score = match.scores[0].value;
            const TeamB = match.competitors[1];
            TeamB.score = match.scores[1].value;    
            const inputScoreA = useRef();
            const inputScoreB = useRef();
            

            
            return(
                <div className="Match flex" onMouseLeave={
                    ()=>{
                        if(cookie.load('login')){
                            axios.post('api/sendBet', {
                                scoreA: inputScoreA.current.value,
                                scoreB: inputScoreB.current.value,
                                idMatch: match.id,
                                user: cookie.load('user')
                            })
                            .then(function (response) {
                                cookie.save('bets', response.data, { path: '/' });
                            })
                            .catch(function (error) {
                                console.log(error);
                            });
                        }
                        

                    }
                }>
                <div className="TeamA flex">
                <img src={TeamA.logo} alt={TeamA.abbreviatedName}/>
                <div className="tag">{TeamA.abbreviatedName}</div>
                <div className="score">
                <div className="value">
                {TeamA.score}
                </div>
                {cookie.load('login') ? <div className="bet">
                <input type="text" name="TeamA" className="scoreInput" ref={inputScoreA} disabled defaultValue={
                    (bets.hasOwnProperty(match.id))?
                        bets[match.id].scoreA :
                 ''
                 } />
                <div className="lilbtn" onClick={
                    ()=>{
                        const score  = parseInt(inputScoreA.current.value);
                        (isNaN(parseInt(inputScoreB.current.value)))? inputScoreB.current.value = 0 : inputScoreB.current.value = inputScoreB.current.value;
                        (isNaN(score))? inputScoreA.current.value = 1 :  (score + 1 > 5)? inputScoreA.current.value = 5 : inputScoreA.current.value = score + 1;
                    }
                }>+</div>
                <div className="lilbtn" onClick={
                    ()=>{
                        const score  = parseInt(inputScoreA.current.value);
                        (isNaN(parseInt(inputScoreB.current.value)))? inputScoreB.current.value = 0 : inputScoreB.current.value = inputScoreB.current.value;
                        (isNaN(score) || (score - 1) < 0)? inputScoreA.current.value = 0 :  inputScoreA.current.value = score - 1;
                    }
                }>-</div>
                </div>:'' }
                </div>
                </div>
                <div className="separator">
                </div>
                <div className="TeamB flex">
                <img src={TeamB.logo} alt={TeamB.abbreviatedName}/>
                <div className="score">
                
                <div className="value">
                {TeamB.score}
                </div>
                {cookie.load('login') ? 
                <div className="bet">
                <input type="text" name="TeamB" className="scoreInput" ref={inputScoreB} defaultValue={
                    (bets.hasOwnProperty(match.id))?
                        bets[match.id].scoreB :
                 ''
                 }/>
                <div className="lilbtn" onClick={
                    ()=>{
                        const score  = parseInt(inputScoreB.current.value);
                        (isNaN(parseInt(inputScoreA.current.value)))? inputScoreA.current.value = 0 : inputScoreA.current.value = inputScoreA.current.value;
                        (isNaN(score))? inputScoreB.current.value = 1 :  (score + 1 > 5)? inputScoreB.current.value = 5 : inputScoreB.current.value = score + 1;
                    }
                }>+</div>
                <div className="lilbtn" onClick={
                    ()=>{
                        const score  = parseInt(inputScoreB.current.value);
                        (isNaN(parseInt(inputScoreA.current.value)))? inputScoreA.current.value = 0 : inputScoreA.current.value = inputScoreA.current.value;
                        (isNaN(score) || (score - 1) < 0)? inputScoreB.current.value = 0 :  inputScoreB.current.value = score - 1;
                    }
                }>-</div>
                </div>
                :'' }
                </div>
                <div className="tag">{TeamB.abbreviatedName}</div>
                </div>
                <div className="myBets">
                </div>
                </div>
                )
            }
            
            export default Weeks;