import React, {useRef, useState} from 'react';
import { Link, Redirect, withRouter } from 'react-router-dom';
import cookie from 'react-cookies'

const axios = require('axios');


// https://api.overwatchleague.com/teams

function Teams(){
    
    const [teams,setTeams] = useState(null);
    
    
    if(teams === null){
        axios.get('https://api.overwatchleague.com/teams')
        .then(function (response) {
            setTeams(response.data.competitors);
        })
        .catch(function (error) {
            console.log(error);
        });
        return (
            <div>
            <h2>Teams</h2>
            </div>
            );
        }else{
            const arrayTeams = teams.map((team)=>{
                return (<Team team={team.competitor} key={team.competitor.id}></Team>)
            })
            
            return(<div className="Teams">
                <h2>Teams</h2>
                <div className="listTEams flex center six">
                {arrayTeams}
                </div>
                
                </div>)
                
            }
        }
        
        function Team({team}){
            console.log(team)
            return(<div className="team">
                {/* {team.abbreviatedName} */}
                <div className="name">{team.name}</div>
                <div className="img"><img src={team.logo} alt={team.alt}/></div>
                </div>)
            }
            
            
            export default withRouter(Teams);